import Vue from 'vue'
import Router from 'vue-router'
import Home from '@/components/home'
import Music from '@/components/music'
import Vido from '@/components/vido'
import Diant from '@/components/diant'
import Parend from '@/components/parend'
import Accout from '@/components/accout'
import Login from '@/components/login'
import Regist from '@/components/regist'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: '首页',
      component: Home,
      children:[
        {
        path: 'vido',
        name: '视频',
        component: Vido,
      },
      {
        path: 'diant',
        name: '电台',
        component: Diant,
      }
    ]
    },
    {
      path: '/music',
      name: '音乐',
      component: Music
    },
    {
      path: '/parend',
      name: '朋友',
      component: Parend
    },
    {
      path: '/accout',
      name: 'accouts',
      component: Accout,
      meta:{
        showFooter:false
      }
    },
    {
      path: '/login',
      name: 'login',
      component: Login
    },
    {
      path: '/regist',
      name: 'Regist',
      component: Regist
    }
  ]
})



