<template>
    <div class="music-box">
         <div class="music-top">
            <span class="more">更多</span>
            <span class="mymusic">我的音乐</span>
            <canvas width="24" height="22"></canvas>
          </div>
          <div class="music-lat">
              <div class="lat-h">
                  <img src="../../img/music-gray.png" alt="" class="music-l">
                  <div class="lat-right">
                      <span>本地音乐</span>
                      <span>77<img src="../../img/jtou-right.png" alt="" class="music-r"> </span>
                  </div>
              </div>
              <div class="lat-h">
                  <img src="../../img/play-l.png" alt="" class="music-l">
                  <div class="lat-right">
                      <span>最近播放</span>
                      <span>70<img src="../../img/jtou-right.png" alt="" class="music-r"> </span>
                  </div>
              </div>
              <div class="lat-h">
                   <img src="../../img/musif-l.png" alt="" class="music-l">
                  <div class="lat-right">
                      <span>我的电台</span>
                      <span>0<img src="../../img/jtou-right.png" alt="" class="music-r"> </span>
                  </div>
              </div>
              <div class="lat-h">
                   <img src="../../img/play-l.png" alt="" class="music-l">
                  <div class="lat-right noboder">
                      <span>我的收藏</span>
                      <span>0<img src="../../img/jtou-right.png" alt="" class="music-r"> </span>
                  </div>
              </div>
              <div class="creatlist">
                    <img src="../../img/huitou.png" alt="">
                    <span>我创建的歌单(2)</span>
              </div>
              <div class="creatlist-box">
                  <div class="creat-s" v-for="i,index in creatlist" :key="index">
                      <img :src="i.img" alt="" class="title-log">
                      <div class="play-div">
                          <p class="title">我喜欢的音乐{{i.num}}</p>
                          <p class="d-num">
                             <img src="../../img/downl.png" alt="" class="downl">{{i.dowlnum}}首,已下载{{i.alreay}}首
                          </p>
                      </div>
                      <img src="../../img/autop.png" alt="" class="autoyu">
                  </div>
              </div>
          </div>
    </div>
</template>
<script>
export default {
     mounted(){
        this.$attrs.appfu.gets=true;
    },
    data(){
        return{
            creatlist:[
                {
                    img:'http://img5.imgtn.bdimg.com/it/u=1783741574,2767026355&fm=27&gp=0.jpg',
                    num:1,
                    dowlnum:192,
                    alreay:44
                },
                {
                    img:'http://img5.imgtn.bdimg.com/it/u=1783741574,2767026355&fm=27&gp=0.jpg',
                    num:2,
                    dowlnum:19,
                    alreay:1
                },
                {
                    img:'http://img5.imgtn.bdimg.com/it/u=1783741574,2767026355&fm=27&gp=0.jpg',
                    num:3,
                    dowlnum:193,
                    alreay:0
                }
            ]
        }
    }
}
</script>




