import Vue from 'vue';
import Vuex from 'vuex';
import { start } from 'repl';
import { stat } from 'fs';
Vue.use(Vuex);

const state={//要设置的全局访问的state对象
   showFooter:true
};

//this.$store.state.changenum获取changenum里的值


// vuex官方API提供的一个getters，实时监听state值的变化
const getters={//实时监听state值的变化最新状态
    
};

//mutations对象,对象里面放改变state初始值的方法，具体用法是给里面的方法传参数，state和额外的参数
//this.$store.commit('clickshow')改变showFooter的值
const mutations={
    upshow(state,msg){
        state.showFooter=msg
    }
    
};

// action执行异步操作
//外部组件进行全局执行action里面方法时  this.$store.dispatch('showFooter')就可以改变方法的值
const actions={
   
};





const store=new Vuex.Store({
    state,
    getters,
    mutations,
    actions
});

export default store;





