<template>
    <!-- 视频 -->
    <div class="video-box">
        <div class="video-div">
            <video src="../../video/movie.mp4" controls></video>
        </div>
        <div class="bottomnav">
            <p class="vname">{{vido.viname}}</p>
            <ul class="right">
                <li>
                    <img src="../../img/zan.png" alt="" class="zan">
                    <p>{{vido.zanum}}</p>
                </li>
                <li>
                <img src="../../img/plun.png" alt="" class="plun">
                    <p>{{vido.plunum}}</p>
                </li>
                <li class="lidian"><span>...</span></li>
            </ul>
        </div>
        <div class="nextTick">
            <div ref="msgDiv">{{msg}}</div>
            <div v-if="msg1">Message got outside $nextTick: {{msg1}}</div>
            <div v-if="msg2">Message got inside $nextTick: {{msg2}}</div>
            <div v-if="msg3">Message got outside $nextTick: {{msg3}}</div>
            <button @click="changeMsg">
                改变信息
            </button>
        </div>
    </div>
</template>
<script>
export default {
    // beforeCreate: function(){
    //       console.log('创建vue实例前',this);
    // },
    // created: function(){
    //       console.log('创建vue实例后',this);
    // },
    // beforeMount:function(){
    //       console.log('挂载到dom前',this);
    // },
    // mounted: function(){
    //       console.log('挂载到dom后',this);
    // },
    // beforeUpdate:function(){
    //       console.log('数据变化更新前',this);
    // },
    // updated:function(){
    //     showData('数据变化更新后',this);
    // },
    // beforeDestroy:function(){
       
    //       console.log('vue实例销毁前',this);
    // },
    // destroyed:function(){
    //     console.log('vue实例销毁后',this);
    // },
    data(){
        return{
            vido:{
                viname:'动物世界',
                zanum:'10',
                plunum:'234'
            },
            msg: 'Hello Vue.',
            msg1: '',
            msg2: '',
            msg3: ''
        }
    },
    methods:{
        changeMsg() {
        this.msg = "Hello world."
        this.msg1 = this.$refs.msgDiv.innerHTML
        this.$nextTick(() => {
            this.msg2 = this.$refs.msgDiv.innerHTML
        })
        this.msg3 = this.$refs.msgDiv.innerHTML
        }
    }
}
</script>




