<template>
    <div class="login-box">
         <div class="login-top">
             <img src="../../img/white-j.png" alt="" @click="reback">
             <p>手机号注册</p>
          </div>
          <div class="login-input" ref="fall">
              <div>
                  <img src="../../img/ipone.png" alt="">
                  <input type="text" placeholder="请输入手机号" ref="mobiles" @blur="moblur">
              </div>
               <p ref="mos">{{mos}}</p>
              <div>
                   <img src="../../img/password.png" alt="">
                  <input type="text" placeholder="请输入密码" ref="pass" @blur="passblur">
              </div>
               <p ref="pos">{{pas}}</p>
               <div>
                   <img src="../../img/password.png" alt="">
                  <input type="text" placeholder="请确认密码" ref="passw" @blur="passblurw">
              </div>
               <p ref="posw">{{pasw}}</p>
          </div>
          <button class="login-bt" @click="logins">注册</button>
    </div>
</template>
<script>
export default {
    mounted() {
        // console.log(this.$refs.mos,'ll')
    },
    data(){
        return{
        mos:'请输入手机号',
        pas:'请输入密码',
        pasw:'请确认密码'
        }
    },
    methods:{
        reback(){
            this.$router.go(-1);
        },
          moblur(){
            console.log(this,'dd');
            // 验证手机号
            if(this.$refs.mobiles.value==''){
                this.$refs.mos.style.display='block';
                this.mos='请输入手机号码'
            }else if(!/^((1[3,5,8][0-9])|(14[5,7])|(17[0,6,7,8])|(19[7]))\d{8}$/.test(this.$refs.mobiles.value)){
                 this.$refs.mos.style.display='block';
                 this.mos='手机格式不正确'
            }else{
                 this.$refs.mos.style.display='none';
            }
        },
        passblur(){
            // 验证密码
             if(this.$refs.pass.value==''){
                  this.$refs.pos.style.display='block';
                 this.pas='请输入密码'
            }else
            if(!/^\d{6}$/.test(this.$refs.pass.value)){
                   this.$refs.pos.style.display='block';
                     this.pas='请输六位数子'
            }else{
                 this.$refs.pos.style.display='none';
            }
        },
        passblurw(){
            // 验证密码
             if(this.$refs.passw.value==''){
                  this.$refs.posw.style.display='block';
                 this.pasw='请确认密码'
            }else
            if(this.$refs.passw.value!=this.$refs.pass.value){
                   this.$refs.posw.style.display='block';
                     this.pasw='两次密码不一致'
            }else{
                 this.$refs.posw.style.display='none';
            }
        },
        logins(){

           if(this.$refs.mobiles.value==''&&this.$refs.pass.value==''&&this.$refs.passw.value==''){
                 this.$refs.mos.style.display='block';
                 this.mos='请输入手机号码';
                this.$refs.pos.style.display='block';
                 this.pas='请输入密码';
                this.$refs.posw.style.display='block';
                 this.pasw='请确认密码'
            }

            if(/^\d{6}$/.test(this.$refs.pass.value)&&this.$refs.passw.value==this.$refs.pass.value
            &&/^((1[3,5,8][0-9])|(14[5,7])|(17[0,6,7,8])|(19[7]))\d{8}$/.test(this.$refs.mobiles.value)
            &&this.$refs.mobiles.value&&this.$refs.pass.value&&this.$refs.passw.value){
                alert('注册成功');
                this.$refs.mobiles.value='',
                this.$refs.pass.value='',
                this.$refs.passw.value='',
                this.$refs.mos.style.display='none';
                this.$refs.pos.style.display='none';
                this.$refs.posw.style.display='none';
            }
          
        }
    }
}
</script>


