<template>
    <div class="lunbo-box">
         <div class="shuffling">
            <Swiper v-if="imgArray.length > 0">
                <Slide v-for="(item,index) in imgArray" :key="index">
                    <img :src="item" alt="">
                </Slide>
            </Swiper>
        </div>
    </div>
</template>
<script>
import { Swiper, Slide } from 'vue-swiper-component';
import { setInterval, clearInterval } from 'timers';
export default {
    components:{
        Swiper,
        Slide
    },
    data(){
        return{
             imgArray:[
            'http://img5.imgtn.bdimg.com/it/u=3303490552,2643456935&fm=27&gp=0.jpg',
            'http://img5.imgtn.bdimg.com/it/u=3303490552,2643456935&fm=27&gp=0.jpg',
            'http://img0.imgtn.bdimg.com/it/u=4264003133,22410145&fm=27&gp=0.jpg',
            'http://img5.imgtn.bdimg.com/it/u=1783741574,2767026355&fm=27&gp=0.jpg'
            ],
        }
    }
}
</script>




