<template>
    <div class="login-box">
         <div class="login-top">
             <img src="../../img/white-j.png" alt="" @click="reback">
             <p>手机号登录</p>
          </div>
          <div class="login-input">
              <div>
                  <img src="../../img/ipone.png" alt="">
                  <input type="number" placeholder="手机号" ref="mobiles" @blur="moblur">
              </div>
              <p ref="mos">{{mos}}</p>
              <div>
                   <img src="../../img/password.png" alt="">
                  <input type="password" placeholder="密码" ref="pass" @blur="passblur">
              </div>
               <p ref="pos">{{pas}}</p>
          </div>
          <button class="login-bt" @click="logins">登录</button>
          <p class="rest">重置密码</p>
    </div>
</template>
<script>
export default {
    created(){
     
    },
    data(){
        return{
            mos:'',
            pas:''
        }
    },
    methods:{
        reback(){
            this.$router.go(-1);
        },
        moblur(){
            console.log(this,'dd');
            // 验证手机号
            if(this.$refs.mobiles.value==''){
                this.$refs.mos.style.display='block';
                this.mos='请输入手机号码'
            }else if(!/^((1[3,5,8][0-9])|(14[5,7])|(17[0,6,7,8])|(19[7]))\d{8}$/.test(this.$refs.mobiles.value)){
                 this.$refs.mos.style.display='block';
                 this.mos='手机格式不正确'
            }else{
                 this.$refs.mos.style.display='none';
            }
        },
        passblur(){
            // 验证密码
             if(this.$refs.pass.value==''){
                  this.$refs.pos.style.display='block';
                 this.pas='请输入密码'
            }else
            if(!/^\d{6}$/.test(this.$refs.pass.value)){
                   this.$refs.pos.style.display='block';
                     this.pas='请输六位数子'
            }else{
                 this.$refs.pos.style.display='none';
            }
        },
        logins(){
            console.log(1)
            if(this.$refs.mobiles.value&&this.$refs.pass.value){
                alert('登录成功');
            }
        }
    }
}
</script>



