<template>
    <div class="home-box">
        <div class="home-warpbox">
          <div class="home-top">
            <img src="../../img/voice.png" alt="" class="voiceimg">
            <div class="search-box">
              <img src="../../img/sou.png" alt="" class="souimg">
                <input type="text" placeholder="搜索歌词、音乐、电台">
            </div>
            <canvas width="24" height="22"></canvas>
          </div>
          <div class="tabs" id="head-nav">
            <ul>
              <li v-for="(item,index) in list" v-on:click="addcli(index)" :class="{'addline':item.show?'addline':''}">{{item.title}}</li>
              
            </ul>
          </div>
        </div>
       
        <div class="home-disbox"  v-show="isSelecteddiv">
          <!-- 音乐 -->
             <lunbo/>

              <div class="radio-station">
                  <div>
                    <img src="../../img/smv.png" alt="">
                    <p>私人FM</p>
                  </div>
                  <div>
                    <img src="../../img/dayt.png" alt="">
                    <p>每日推荐</p>
                  </div>
                  <div>
                    <img src="../../img/playlist.png" alt="">
                    <p>歌单</p>
                  </div>
                  <div>
                    <img src="../../img/list.png" alt="">
                    <p>排行榜</p>
                  </div>
              </div>
              <div class="recom-list">
                <span class="suc"></span>
                <span class="list-title">推荐歌单</span>
                <img src="../../img/jtou.png" alt="">
              </div>
              <div class="playlist-recom">
                <ul>
                    <li v-for="item,index in playlist" :key="index">
                      <img :src="item.img" alt="" class="tuping">
                      <p class="persum"><img src="../../img/headset.png" alt="">{{item.pernums}}万</p>
                      <p class="play-cs">{{item.playname}}</p>
                    </li>
                 </ul>
              </div>

              <div class="recom-list">
                <span class="suc"></span>
                <span class="list-title">推荐MV</span>
                <img src="../../img/jtou.png" alt="">
              </div>
              <div class="playlist-recom">
                <ul>
                  <li v-for="item,index in jplaylist" :key="index">
                      <img :src="item.img" alt="" class="tuping">
                      <p class="persum"><img src="../../img/headset.png" alt="">{{item.pernums}}万</p>
                      <p class="play-cs">{{item.playname}}</p>
                    </li>
                </ul>
              </div>
        </div>
        <router-view/>
    </div>
</template>
<script>
import Lunbo from './lunbo'
export default {
  mounted(){
    // 挂载到dom后
    this.$attrs.appfu.gets=true;
    
  },
  components:{
       Lunbo
  },
  data () {
    return {
      list:[
        {title:'音乐',show:true},
        {title:'视频',show:false},
        {title:'电台',show:false}
        ],
     
      playlist:[
           {
             img:'http://img5.imgtn.bdimg.com/it/u=407910219,178769022&fm=27&gp=0.jpg',
             pernums:'20',
             playname:'我是你的什么人'
           },
           {
             img:'http://img5.imgtn.bdimg.com/it/u=407910219,178769022&fm=27&gp=0.jpg',
             pernums:'20',
             playname:'我是你的什么人'
           },
           {
             img:'http://img5.imgtn.bdimg.com/it/u=407910219,178769022&fm=27&gp=0.jpg',
             pernums:'20',
             playname:'我是你的什么人'
           },
           {
             img:'http://img5.imgtn.bdimg.com/it/u=407910219,178769022&fm=27&gp=0.jpg',
             pernums:'20',
             playname:'我是你的什么人'
           },
           {
             img:'http://img5.imgtn.bdimg.com/it/u=407910219,178769022&fm=27&gp=0.jpg',
             pernums:'20',
             playname:'我是你的什么人'
           },
           {
             img:'http://img5.imgtn.bdimg.com/it/u=407910219,178769022&fm=27&gp=0.jpg',
             pernums:'20',
             playname:'我是你的什么人'
           },
      ],
     jplaylist:[
           {
             img:'http://img5.imgtn.bdimg.com/it/u=407910219,178769022&fm=27&gp=0.jpg',
             pernums:'20',
             playname:'甜蜜的回忆'
           },
           {
             img:'http://img5.imgtn.bdimg.com/it/u=407910219,178769022&fm=27&gp=0.jpg',
             pernums:'1',
             playname:'没有如果'
           },
           {
             img:'http://img5.imgtn.bdimg.com/it/u=407910219,178769022&fm=27&gp=0.jpg',
             pernums:'10',
             playname:'拯救'
           },
           {
             img:'http://img5.imgtn.bdimg.com/it/u=407910219,178769022&fm=27&gp=0.jpg',
             pernums:'10',
             playname:'拯救'
           },
           {
             img:'http://img5.imgtn.bdimg.com/it/u=407910219,178769022&fm=27&gp=0.jpg',
             pernums:'10',
             playname:'拯救'
           },
           {
             img:'http://img5.imgtn.bdimg.com/it/u=407910219,178769022&fm=27&gp=0.jpg',
             pernums:'10',
             playname:'拯救'
           }
      ],
      isSelecteddiv:true
    }
  },
  methods:{
    // 增加类
   addcli(index){
    //  同级删除类
    for(let i=0;i<this.list.length;i++){
      this.list[i].show=false;
    };
    // 点击该索引增加类
    this.list[index].show=true;
   
    if(index){
        this.isSelecteddiv=false;
    }else{
       this.isSelecteddiv=true;
    };

    if(index==0){
      this.$router.push('/')
    }else
    if(index==1){
        this.$router.push({path:'/vido'})
    }else if(index==2){
      this.$router.push({path:'/diant'})
    };
   }
  }
}
</script>



